import React, { Component } from 'react'
import alphabets from './alphabets.json'
import classNames from 'classnames'

class EasyABC extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             alphabets: alphabets,
             currentPosition: 0,
             currentTick: 0,
             random: false
        }
    }

    randomNumber(){
        return Math.floor(Math.random()*25)+1;
    }

    nextImgHandler(){
        if(this.state.random){
            if(this.state.currentTick<2){
                this.setState({currentTick: this.state.currentTick+1})
            }else{
                this.setState({currentPosition: this.randomNumber(),currentTick: 0})
            }
        }else{
            console.log(`i got clicked`)
            if(this.state.currentTick<2){
                this.setState({currentTick: this.state.currentTick+1})
            }else{
    
                if(this.state.currentPosition===25){
                    this.setState({currentPosition: 0,currentTick: 0})
                }else{
                    this.setState({currentPosition: this.state.currentPosition+1,currentTick: 0})
                }
            }
            
        }
    }

    prevImgHandler(){
        if(this.state.currentPosition>0){
            this.setState({currentPosition: this.state.currentPosition - 1,currentTick: 0})
        }else{
            this.setState({currentPosition: 25,currentTick: 0})
        }
    }

    switchRandom(){
        this.setState({
            random: !this.state.random
        })
    }
    
    resetHandler(){
        this.setState({
            alphabets: alphabets,
            currentPosition: 0,
            currentTick: 0,
            random: false
        })
    }

    render() {
        let showImg = this.state.currentTick !== 0 ? true : false;
        let showWord = this.state.currentTick < 2 ? true : false;
        return (
            <div className="game">
                <span className="random-label">Random Letters:</span>
                <label className="switch">
                    <input type="checkbox"
                    onClick={()=>this.switchRandom()}
                    defaultValue="false"
                    checked={this.state.random} />
                    <div className="slider round"></div>
                </label>
                <div className="option">
                    <div className="fields">
                        <div className="field-block">
                            {this.state.alphabets[this.state.currentPosition].letter}
                        </div>
                    </div>
                    <div className="buttons">
                        <button onClick={()=>this.prevImgHandler()} className="button prev">Previous</button>
                        <button onClick={()=>this.resetHandler()} className="button sound">Reset Game</button>
                        <button onClick={()=>this.nextImgHandler()} className="button next">Next</button>
                    </div>
                    <div className="fields">
                        <div className="field-block">
                            <div className="left-field">
                                <div className={classNames('placeholder-span',{hide: showImg})}>Click Next to view Image</div>
                                <img className={classNames('letter-image',{hide: !showImg})}
                                 src={this.state.alphabets[this.state.currentPosition].image} alt={this.state.alphabets[this.state.currentPosition].letter}/>
                            </div>
                            <div className="right-field">
                                <div className={classNames('placeholder-span',{hide: !showWord})}>Click Next to view Spelling</div>
                                <div className={classNames('word',{hide: showWord})}>
                                    {this.state.alphabets[this.state.currentPosition].word.toUpperCase()}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default EasyABC;
